.flexElements{
  display:flex;
  background-color:#2a2a38;
  flex-direction: row;
  grid-gap: 20px; 

}

.image-cont {
  flex: 1;
  text-align: center;
  background-color: #4e4e80d0;
  border-radius: 100px;
  margin: 10px;
  box-shadow: 8px 8px 8px rgba(0, 0, 0, 0.5);
  
  
  
  
}
.image-cont >img {
  flex: 1;
  width:100%;
  border-radius: 100px;
  padding: 1%;
  padding-bottom: 0px;
  
}

.loadcontent
{

  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /*background-color: #4b4b97;*/
  border-radius: 100px;
  
  /*margin: 300px;*/

}
.loadcontent>h2 {
  flex:1;
  position: absolute;
  font-size: 30px;
  margin-top: -230px ;
  margin-bottom: -2px;
  color: #ffffff;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande',
  'Lucida Sans', Arial, sans-serif
  
}
.loadcontent>p {
  flex:1;
  position: absolute;
  font-size: 15px;
  margin-bottom: 20px;
  margin-top: 120px ;
  color: #ffffff;
  font-style: italic;
  font-family: Arial, sans-serif
  
}
/* Circular loading indicator */
.progress-circle2 {
  position: absolute;
  width: 100px;
  height: 100px;
  margin-top: -50px;
  animation: rotate 2s linear infinite;
}

.circle2 {
  
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: transparent;
  position: absolute;
  border: 4px solid #ffffffc4; /* Loading circle color */
}

.mask2 {
  width: 50%;
  height: 100%;
  position: absolute;
  clip: rect(0, 50px, 100px, 0px); /* Clip the circle to create the loading effect */
}

.fill2 {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.212); /* Loading fill color */
  position: absolute;
  clip: rect(0, 100px, 100px, 50); /* Clip the fill to create the loading effect */
}







.unity-container2 {
  display: flex;
  flex:1;
  background-color: #4b4b97;
  border-radius: 100px;
  aspect-ratio: 3/4;
  
  margin: 10px;
  
  
  
}

.content2{
  

  display: flex; /* Use flexbox for centering */
  flex:1;
  justify-content: center; /* Center vertically */
  align-items: center; /* Center horizontally */
  background-color: #ffffff; /* Set background color */
  padding: 20px; /* Add padding for spacing */
  box-sizing: border-box; /* Include padding in width calculation */
  border-radius: 100px;

}

/* Hide the loading screen when Unity is loaded */
.unity-loaded .loading-screen {
  display: none;
}


@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@media screen and (max-width: 768px) {
  .flexElements{
      display:flex;
      flex-direction: column;
      grid-gap: 5px; 
    
    }


  .load-content
  {
      flex: 1;
      text-align: center;
      border-radius: 100px;
      margin: 10px;
  

  }
  .image-cont {
      flex: 1;
      text-align: center;
      background-color: #4b4b97;
      border-radius: 100px;
      margin: 10px;
    
  }
  .image-cont >img {
      flex: 1;
      
      position:relative;
      z-index: 1;
  
    }

  .unity-container2 {
    display: flex;
    flex:1;
    text-align: center;
    
  }
  
  .content2{
    display: flex; /* Use flexbox for centering */
    flex:1;
    justify-content: center; /* Center vertically */
    align-items: center; /* Center horizontally */
    background-color: #ffffff; /* Set background color */
  
  }
  .loadcontent
  {

    margin-top:280px;

  }
  .progress-circle2 {
  
    
    width: 100px;
    height: 100px;
    margin-top: -180px;
  }

  .loadcontent>h2 {
    flex:1;
    font-size: 30px;
    margin-top:-400px ;
    margin-bottom: 20px;
    
    
  }
  .loadcontent>p {
    flex:1;
    font-size: 15px;
    margin-bottom:50px;
    margin-top: 10px ;
    
  }
  }